.flex-center {
  display: flex;
  justify-content: center;
}

.flex-space-evenly {
  display: flex;
  justify-content: space-evenly;
}

.flex-space-between {
  display: flex;
  justify-content: space-between;
}

.flex-space-around {
  display: flex;
  justify-content: space-around;
}

.row {
  display: grid;
  width: 100%;
  margin: 0 auto;
  grid-template-columns: repeat(12, 1fr);
  grid-column-gap: .5em;
}

